
.hero {
    padding-bottom: 6rem;

    p {
        font-size: 4rem;
        font-weight: 400;
        @media (max-width: 568px) {
           font-size: 3rem;
        }

        &:first-child {
            padding-bottom: 1.5rem;
        }

        &:last-child {
            font-size: 2.75rem;
            font-weight: 400;
            @media (max-width: 568px) {
               font-size: 1.75rem;
            }
        }
    }
    
    .container {
       display: flex;
       flex-direction: column;
    }
    
    .container--small {
        max-width: 75vw;
        max-width: 768px;
    }
}