$purple: #523dc5;
$black: #38393d;
$blackLight: #4b4d53;
$white: #fff;
$yellow: #F7EB49;
$yellowLight: #faf78e;
$fuchsia: #e002a3;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
    font-family: 'DM Sans', sans-serif;
    font-weight: 400;
    font-size: 1rem;
    color: $black;
    //padding-top: calc(0.5rem + 117px);
    
    // @media (max-width: 568px) {
    //      padding-top: calc(0.5rem + 154px);
    // }
}

small {
    font-size: 0.875rem;
}

button {
    font-family: 'DM Sans', sans-serif;
    border: 0;
}

h1 {
    font-weight: 700;
    font-size: 1rem;
}

h3 {
    font-weight: 700;
    font-size: 1rem;
}

a {
    text-decoration: none
}

.container {
	padding-left: 1rem;
    padding-right: 1rem;
    width: 90vw;
    max-width: 1024px;
    margin: 0 auto;
}

footer {
	padding-top: 3rem;
    padding-bottom: 3rem;
}

