.work-experience {
    h3 {
        margin-bottom: 1rem;
    }

    a {
        color: $fuchsia;
        display: inline-flex;
        align-items: center;
        transition: color ease-in-out 250ms, box-shadow ease-in-out 250ms;
        box-shadow: inset 0 0 0 0 $fuchsia;
        margin: 0 -0.25rem;
        padding: 0 0.25rem;

        &:hover {
            color: $white;
            box-shadow: inset 25rem 0 0 0 $fuchsia;
        }
    }
}

.accordion {
    position: sticky;
    top: 0.25rem;
    cursor: pointer;
    width: 100%;
    padding: 1rem;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 400;
    font-size: 1.375rem;
    
    @media (max-width: 568px) {
       font-size: 1.125rem;
    }

    &:after {
        content: '\002B';
        font-size: 1.75rem;
        @media (max-width: 568px) {
            font-size: 1.25rem;
        }
    }

    &.active {
        background-color: #ebebec;
        &::after {
            content: "\2212";
        }
    }

    &:hover {
        background-color: #c3c4c5;
    }
}
  
.panel {
    height: auto;
    max-height: 0;
    overflow: hidden;
    transition: max-height 500ms ease-in-out;
    margin-bottom: 0.25rem;

    &.active {
        max-height: 2000vh;
    }

    .panel-partition {
        padding: 1rem;
        max-width: 640px;

        &.select-projects {
            max-width: 100%;

            .project {
                padding-top: 0.5rem;
                display: flex;
                gap: 1rem;

                figure {
                    max-width: 320px;
                    border: 0.125rem $black solid;
                    border-radius: 0.125rem;

                    div {
                        display: flex;
                        justify-content: flex-end;
                        padding: 0.25rem;
                        gap: 0.5rem;
                        border-bottom: 0.125rem $black solid;

                        span {
                            width: 0.5rem;
                            height: 0.5rem;
                            border-radius: 100%;
                            background-color: $black;
                        }
                    }

                    img {
                        width: 100%;
                    }
                }

               figcaption {
                    color: $blackLight;

                    p {
                        &:first-child {
                            padding-top: 0.25rem;
                        }

                        padding-bottom: 0.5rem;
                    }

                    a {
                        font-size: 0.875rem;

                        span {
                            font-size: 1rem;
                        }
                    }
                }
            }
        }

        small {
            font-weight: 700;
            line-height: 1.5;
            font-size: 1.125rem;
        }

        p {

        }
    }
}